*,::before,::after {
  box-sizing: border-box;
}

img {
  vertical-align: middle;
  max-width:100%;
}

body {
  font-family: $font-noto, $font-roboto;
  font-size: $fz-base;
  line-height: $lh-normal;
  color: $primary-font;
}

a {
  text-decoration:none;
  color:inherit;
  &:hover {
    color: $hover-color;
  }
}
